
h {
  background: yellow;
}
.control-header {
  width: 100%;
  height: 121px;
  padding: 12px 17px 0 17px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
.control-header .list {
    background: white;
    text-align: center;
    display: inline-block;
    width: 75px;
    height: 34px;
    border-radius: 2px;
    box-shadow: 0 0 4px 3px #e0e2e4;
    margin-right: 12px;
}
.control-header .list span {
      line-height: 34px;
      font-size: 14px;
      color: #3A3434;
}
.control-header .group {
    background: white;
    text-align: center;
    display: inline-block;
    width: calc(100% - 92px);
    height: 34px;
    border-radius: 2px;
    box-shadow: 0 0 4px 3px #e0e2e4;
}
.control-header .group span {
      line-height: 34px;
      font-size: 14px;
      color: #3A3434;
}
.control-header .expand {
    display: flex;
    position: relative;
    flex-direction: row;
    justify-content: space-around;
    height: 64px;
    width: calc(100% - 92px);
    background: white;
    border-radius: 2px;
    box-shadow: 0 0 4px 3px #e0e2e4;
    margin-left: 92px;
    margin-top: 10px;
    padding: 12.5px 10px;
}
.control-header .expand .expand-button {
      display: block;
      width: 35px;
}
.control-header .expand .expand-button img {
        display: block;
        width: 30px;
        height: 30px;
        margin: 0 auto 2.5px auto;
}
.control-header .expand .expand-button span {
        display: block;
        font-size: 8px;
        color: #686B6D;
}
.control-header .expand::before {
      content: '';
      width: 0;
      height: 0;
      display: block;
      position: absolute;
      top: -5px;
      left: calc(50% - 2.5px);
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-bottom: 5px solid white;
}
.main-scroll {
  display: block;
  overflow: scroll;
  height: 667px;
}
.course-info-header {
  background: white;
  height: 144px;
  padding-top: 62px;
}
.course-info-header .title {
    color: #3A3434;
    font-size: 18px;
    font-weight: 500;
}
.course-info-header .info {
    width: 165px;
    margin: 12px auto;
    color: #3A3434;
    font-size: 14px;
    font-weight: 500;
    padding: 4px;
    border-radius: 18px;
    border: solid 1px #3A3434;
}
.course-info-header .time {
    color: #E0B190;
    font-weight: 500;
}
.messages-scroll {
  padding: 16px 17px 75px 17px;
  background: #f0f0f1;
  min-height: 523px;
}
.messages-scroll .message {
    margin: 16px 0;
}
.messages-scroll .message:first-child {
    margin: 0 0 16px 0;
}
.messages-scroll .bottom-hint {
    text-align: center;
    height: 25px;
    width: 100%;
    background: #E7EAEC;
    color: #686B6D;
    font-size: 12px;
    line-height: 25px;
}
div.comment-panel {
  position: fixed;
  bottom: 71px;
  right: 12px;
}
div.comment-input {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 59px;
  position: fixed;
  bottom: 0;
  left: 0;
  background: white;
  box-shadow: 0 0 4px 3px #e0e2e4;
}
div.comment-input .input {
    display: block;
    border: none;
    overflow: auto;
    outline: none;
    font-size: 16px;
    width: calc(100% - 114px);
    height: 45px;
    color: #686B6D;
}
div.comment-input .pre-icon {
    display: block;
    width: 21px;
    height: 21px;
}
div.comment-input .submit-button {
    display: block;
    width: 45px;
    height: 35px;
    text-align: center;
    color: white;
    font-size: 12px;
    line-height: 35px;
    background: #3A3434;
    border-radius: 3px;
}



